	module.exports = {"data":{"site":{"siteMetadata":{"title":"huan9huan的博客","author":"huang huan"}},"markdownRemark":{"id":"/Users/alhuang/work/blog/src/pages/full-engagement/index.md absPath of file >>> MarkdownRemark","html":"<p>张遇升精品课 - 如何做精力管理的高手 </p>\n<h2>核心摘录</h2>\n<blockquote>\n<p>基础的金字塔模型 - 体能、情绪、注意力和意义感<br>\nyou are what you eat<br>\n每天起来的几个事情 - 回忆梦，喝热水，左手刷牙，单脚站立，吃高蛋白的早餐<br>\n理论：昼夜平衡和内生平衡<br>\n午餐：少吃高热量高碳水化合物的事物，比如米饭，而是吃高蛋白的事物，这样热量的波动小，从而保证精力的稳定，符合人的小周期<br>\n喝水：每天是自己体重的1/32, 我就是4.7L，八瓶600ml的矿泉水<br>\n少吃多餐，也是符合人体小周期<br>\n量化食物：用NQI（营养热量比），从而筛选出绿色蔬菜是优质的食物，而米饭白面的这个值很小<br>\n健身可以无处不在，深蹲50下检验自己的体能是否正常<br>\n健身的量是每周180分钟  </p>\n</blockquote>\n<h2>我目前能做到的</h2>\n<blockquote>\n<p>午餐的把控能力<br>\n喝水<br>\n少吃多餐<br>\n游泳<br>\n注意力管理，特别是如何制造一个无干扰环境<br>\n意义感，方向感  </p>\n</blockquote>\n<h2>需要特别练习的4项</h2>\n<blockquote>\n<p>健身的量太少，目前大约是每周1h<br>\n重视蔬菜<br>\n没事别上床，使用行为认知学提升睡眠水平<br>\n注意力还是不够强，特别是喜欢在微博上消耗过长的时间  </p>\n</blockquote>","frontmatter":{"title":"从张遇升精品课学到的","date":"June 15, 2018"}}},"pathContext":{"slug":"/full-engagement/","previous":{"fields":{"slug":"/back-to-2009/"},"frontmatter":{"title":"2009，曾经写过"}},"next":{"fields":{"slug":"/cs-is-big-hummer/"},"frontmatter":{"title":"CS is a big hummer"}}}}

